import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import "./NavBar.css";
import logo from "../../Assets/Images/svg/Navbar/Logo.svg";
import Container from "../../Components/Common/Container/Container";
import Modal from "../../Components/Common/Modal/Model";
import { toast } from "react-toastify";
// import sideArrow from "../../assets/side-arrow.png";

const NavBar = () => {
  const getPath = useLocation().pathname.split("/")?.[1];

  const navigate = useNavigate();
  const headerData = [
    {
      id: 0,
      pathname: "/",
      pagename: "Home",
    },
    {
      id: 1,
      pathname: "/tool",
      pagename: "Tool",
    },
    {
      id: 2,
      pathname: "/minting",
      pagename: "Minting",
    },
    {
      id: 3,
      pathname: "/staking",
      pagename: "Staking",
    },
  ];

  const [open, setOpen] = useState(false);
  const [scroll, setScroll] = useState(0);

  useEffect(() => {
    const onScroll = () => {
      setScroll(window.scrollY);
    };
    window.addEventListener("scroll", onScroll);

    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
    if (JSON.parse(localStorage.getItem("wallet")) != null) {
      if (JSON.parse(localStorage.getItem("wallet")).wallet === "nami") {
        toast.success("nami wallet connected...!");
      }
      if (JSON.parse(localStorage.getItem("wallet")).wallet === "eternl") {
        toast.success("eternal wallet connected...!");
      }
    }
  }, []);

  return (
    <>
      <nav
        className={`navbar lg:py-2 ${
          scroll > 50 ? "!bg-black" : "bg-transparent"
        }`}
      >
        <div className="navbar-container container mx-auto">
          <input type="checkbox" name="" id="" />
          <div className="hamburger-lines">
            <span class="line line1"></span> <span class="line line2"></span>
            <span class="line line3"></span>
          </div>
          <ul className="menu-items">
            {headerData?.map((data, index) => (
              <>
                <li className="min-w-max" key={index}>
                  <Link to={data.pathname}>
                    <div
                      className={`flex justify-center items-center font-medium md:text-lg rounded-3xl ${
                        `/${getPath}` === data.pathname
                          ? "active"
                          : "text-white"
                      }`}
                    >
                      {data.pagename}
                    </div>
                  </Link>
                </li>
              </>
            ))}

            <button className="text-white font-medium mx-auto px-5 py-2 navBar-btn walletMobile hidden min-w-max">
              <p className="flex justify-center items-center">Connect Wallet</p>
            </button>
          </ul>
          <div
            className="logo grid place-items-center cursor-pointer"
            onClick={() => navigate("/")}
          >
            <div className="lg:w-28 w-20 lg:mx-0 mx-5">
              <img src={logo} alt="" />
            </div>
          </div>
          <div className="auth-button md:mx-5 mx-2">
            <button
              className="sign text-white font-medium md:mx-5 mx-2 px-5 py-2 navBar-btn"
              onClick={() => setOpen(!open)}
            >
              <p className="flex justify-center items-center">Connect Wallet</p>
            </button>
            <button
              className="join py-2 md:px-5 px-3 rounded-md bg-gradient-to-r from-[#C517D1] to-[#2592D9] text-white font-medium"
              onClick={() => {
                navigate("/log-in");
              }}
            >
              Login
            </button>
          </div>
        </div>
      </nav>

      {open && <Modal close={() => setOpen(!open)} />}
    </>
  );
};
export default NavBar;
