export const ButtonFill = ({ children, onClick }) => {
  return (
    <>
      <button
        className="bg-gradient-to-r from-[#D10ED1] to-[#10A3DA] rounded-lg font-josefinsans"
        onClick={onClick}
      >
        <div className="w-max py-2 px-10 text-center">{children}</div>
      </button>
    </>
  );
};

export const ButtonSimple = ({ children, onClick }) => {
  return (
    <>
      {/*
  
    <button className="gradient-border-rounded" onClick={onClick}>
    <div className="w-max py-2 px-10 text-center">{children}</div>
    </button>
  */}

      <div className="button-wrapper font-josefinsans">
        <button className="button" type="button" onClick={onClick}>
          {children}
        </button>
        <div className="button-bg"></div>
      </div>
    </>
  );
};

