import React, { useState } from "react";
import "./Modal.css";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
let Buffer = require("buffer/").Buffer;

const Modal = ({ close }) => {
  const [namiwallet, setNami] = useState(false);
  const [eternlwallet, setEternl] = useState(false);

  // Convert hexa to address
  const getAddress = async (baseaddr) => {
    const S = await import("@emurgo/cardano-serialization-lib-asmjs");
    return S.Address.from_bytes(Buffer.from(baseaddr, "hex")).to_bech32();
  };

  // SignHash Created
  const signhash = async (data) => {
    try {
      if (data === "nami") {
        localStorage.clear();
        var namiMessage = Buffer.from(
          "9b1deb4d-3b7d-4bad-9bdd-2b0d7b3dcb6d Let me Trade",
          "utf8"
        ).toString("hex");
        var namiApi = await window?.cardano?.nami?.enable();
        var namiPaymentAddr = await namiApi.getChangeAddress();

        var nami_base_addr = await namiApi.getUsedAddresses();
        var nami_stake_base_key = await namiApi.getRewardAddresses();

        // Convert start
        var nami_My_wallet_adress = await getAddress(nami_base_addr[0]);
        console.log(
          " ~ signhash ~ nami_My_wallet_adress",
          nami_My_wallet_adress
        );
        var nami_my_stake_key = await getAddress(nami_stake_base_key[0]);
        // Convert End

        if (JSON.parse(localStorage.getItem("wallet")) == null) {
          var namiResult = await namiApi.signData(namiPaymentAddr, namiMessage);
          localStorage.setItem(
            "wallet",
            JSON.stringify({ wallet: "nami", token: namiResult })
          );
          toast.success("nami wallet connected...!");
        }
        close();
      } else {
        localStorage.clear();
        var eternalMessage = Buffer.from(
          "9b1deb4d-3b7d-4bad-9bdd-2b0d7b3dcb6d Let me Trade",
          "utf8"
        ).toString("hex");
        var eternalApi = await window?.cardano?.eternl?.enable();
        var eternalPaymentAddr = await eternalApi.getRewardAddresses();
        var stake_key = await getAddress(eternalPaymentAddr[0]);
        console.log(" ~ signhash ~ stake_key", stake_key);

        var eternl_base_addr = await eternalApi.getUsedAddresses();
        console.log(" ~ signhash ~ eternl_base_addr", eternl_base_addr);
        if (JSON.parse(localStorage.getItem("wallet")) == null) {
          var result = await eternalApi.signData(
            eternalPaymentAddr,
            eternalMessage
          );
          localStorage.setItem(
            "wallet",
            JSON.stringify({ wallet: "eternl", token: result })
          );
          toast.success("eternal wallet connected...!");
        }
        close();
      }
    } catch (err) {
      console.log(" ~ signhash ~ err", err);
      toast.error("User decline the request");
      close();
    }
  };

  // NamiWallet Connection
  const namiConnect = () => {
    signhash("nami");
  };

  // EternalWallet Connection
  const eternlConnect = () => {
    signhash("eternl");
  };

  return (
    <>
      <div className="fixed backdrop-filter backdrop-blur-sm bg-backdrop flex items-center justify-center overflow-auto z-[9999] inset-0 fadeIn">
        <div className="modal-dialog relative bg-white dark:bg-blue-darkest rounded-xl shadow-xl px-16 py-10 max-w-2xl w-11/12 md:w-full">
          <div className="text-center mb-7">
            <h1 className="modal-header text-blue-700 dark:text-gray-lightest mb-10 font-bold text-3xl">
              Select wallet
            </h1>
          </div>
          <button
            type="button"
            className="absolute text-2xl px-2.5 text-gray-700 top-3 right-3 hover:opacity-100 opacity-70"
            onClick={close}
          >
            <i className="fas fa-times" />
          </button>
          <div className="flex flex-wrap justify-center gap-5">
            {/* Eternal Start */}
            <div>
              <button
                type="button"
                className="wallet-card modal-input relative w-40 p-3 bg-gray-lightest dark:bg-blue-darker rounded-xl text-blue-700 dark:text-gray-regular bg-opacity-60 border-2 hover:bg-opacity-10 dark:hover:bg-blue-meta dark:hover:bg-opacity-20 hover:bg-blue-500 hover:border-blue-500 text-lg font-semibold dark:border-blue-darkest"
                onClick={eternlConnect}
              >
                <img
                  src="data:image/png;base64,"
                  alt="eternl wallet"
                  className="w-16 h-16 p-2 mx-auto mb-2"
                />
                eternl
                <div className="text-xs font-normal mt-1.5 text-gray-dark">
                  <i className="fas fa-unlink" />
                  not enabled
                </div>
              </button>
            </div>
            {/* Eternal End */}

            {/* Nami Start */}
            <div>
              <button
                type="button"
                className="wallet-card modal-input relative w-40 p-3 bg-gray-lightest dark:bg-blue-darker rounded-xl text-blue-700 dark:text-gray-regular bg-opacity-60 border-2 hover:bg-opacity-10 dark:hover:bg-blue-meta dark:hover:bg-opacity-20 hover:bg-blue-500 hover:border-blue-500 text-lg font-semibold dark:border-blue-darkest"
                onClick={namiConnect}
              >
                <img
                  src="data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 486.17 499.86'%3E%3Cdefs%3E%3Cstyle%3E.cls-1%7Bfill:%23349ea3;%7D%3C/style%3E%3C/defs%3E%3Cg id='Layer_2' data-name='Layer 2'%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath id='path16' class='cls-1' d='M73.87,52.15,62.11,40.07A23.93,23.93,0,0,1,41.9,61.87L54,73.09,486.17,476ZM102.4,168.93V409.47a23.76,23.76,0,0,1,32.13-2.14V245.94L395,499.86h44.87Zm303.36-55.58a23.84,23.84,0,0,1-16.64-6.68v162.8L133.46,15.57H84L421.28,345.79V107.6A23.72,23.72,0,0,1,405.76,113.35Z'/%3E%3Cpath id='path18' class='cls-1' d='M38.27,0A38.25,38.25,0,1,0,76.49,38.27v0A38.28,38.28,0,0,0,38.27,0ZM41.9,61.8a22,22,0,0,1-3.63.28A23.94,23.94,0,1,1,62.18,38.13V40A23.94,23.94,0,0,1,41.9,61.8Z'/%3E%3Cpath id='path20' class='cls-1' d='M405.76,51.2a38.24,38.24,0,0,0,0,76.46,37.57,37.57,0,0,0,15.52-3.3A38.22,38.22,0,0,0,405.76,51.2Zm15.52,56.4a23.91,23.91,0,1,1,8.39-18.18A23.91,23.91,0,0,1,421.28,107.6Z'/%3E%3Cpath id='path22' class='cls-1' d='M134.58,390.81A38.25,38.25,0,1,0,157.92,426a38.24,38.24,0,0,0-23.34-35.22Zm-15,59.13A23.91,23.91,0,1,1,143.54,426a23.9,23.9,0,0,1-23.94,23.91Z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E"
                  alt="Nami wallet"
                  className="w-16 h-16 p-2 mx-auto mb-2"
                />
                Nami
                <div className="text-xs font-normal mt-1.5 text-gray-dark">
                  <i className="fas fa-unlink" />
                  not enabled
                </div>
              </button>
            </div>
            {/* Nami End */}
          </div>
        </div>
      </div>
    </>
  );
};

export default Modal;
